  .main_menu_items {
    display: flex;
    flex-direction: column;
    gap: 40px;
  }

@media (min-width: 1120px) {
  .main_menu_items {
    flex-direction: row;
    gap: 8px;
  }
}

/* css for nav link */

.nav-link::after {
  content: '';
  position: absolute;
  bottom: -6px;  
  left: 8px;  
  width: 0;
  height: 4px;
  border-radius: 10px;
  background-color: #a6a1ff;  
}

.nav-link:hover:not(:focus)::after,
.nav-link[aria-current='page']::after {
  width: calc(100% - 16px); 
  transition: width 0.3s ease;  
}

.nav-link {
  font-size: xx-large;
}

@media (min-width: 1120px) {
  .nav-link {
    font-size: medium;
  }
}
